.App {
  align-items: center;
}

/* HEADER SECTION  */
.HeaderStyle {
  align-items: center;
  margin-top: 22px;
  margin-left: 22px;
  margin-right: 22px;
  display: inline-block;
  background-color: powderblue;
  border-radius: 10px;
  padding: 15px
}

.AboutStyle {
  background-color: azure;
  padding: 10px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(11, 65, 104, 0.1);
}

.TechCont {
  display: flex;           
  flex-wrap: wrap;         
  justify-content: center; 
  align-items: center;     
  margin-top: 20px;
  margin-bottom: 10px;
  row-gap: 20px;
  column-gap: 25px;
}

.TechLogo {
  width: 65px;
  height: 65px;
}

/* PROJECTS */
.ProjectsCont {
  height: 615px;
}

.ProjHeader {
  padding: 13px;
  background-color: powderblue;
  border-radius: 5px;
  margin-top: 12px;
  margin-left: 15px;
  margin-right: 15px;
}

.ProjCardsCont {
  padding-top: 25px;
}

/* Basic card */
.ProjCard {
  position: absolute;
  /* transform: translateX(-20%); */
  width: 450px; 
  height: 400px;
  margin-top: 40px;
  transition: transform 0.7s ease, width 0.7s ease, height 0.7s ease, opacity 0.7s ease;
  background-color: azure;
  border: 0.3px solid lightblue;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 17px;
  z-index: 0; 
}

/* Center card styles */
.ProjCard.center { 
  z-index: 1;
}

/* Responsive styles for smaller screens */
@media (max-width: 768px) {
  .ProjCard {
    width: 75%; /* Adjust width for mobile */
    left: 47.2%; /* Center horizontally for mobile */
  }

  .ProjCard img {
    width: 95%; /* Adjust image width for mobile */
    height: auto;
    border-radius: 20px;
  }

  .ProjCard .technologies img {
    width: 55px;
    height: 55px;
  }
}

/* Image and video container styles */
.ProjCard img,
.ProjCard video {
  max-width: 100%;
  max-height: 300px;
  border-radius: 10px;
  margin-top: 40px;
}

.ProjCard .image-container {
  margin-top: 50px;
  max-width: 400px;
}

.ProjCard .video-container {
  margin-top: 50px;
  max-width: 400px;
}

/* Card information styles */
.ProjCard .card-info {
  margin: 20px;
  margin-top: 50px;
  justify-content: space-between;
  height: 100%;
}

/* GitHub link styles */
.ProjCard a {
  display: block;
  margin-top: 10px;
}

/* Technologies container */
.ProjCard .technologies {
  position: absolute;
  bottom: 25px;
  width: 90%;
  display: flex;           
  flex-wrap: wrap;         
  justify-content: center; 
  align-items: center;  
  row-gap: 0px;
}

.ProjCard .technologies img {
  width: 55px;
  height: 55px;
  margin-left: 5%;
  margin-right: 5%;
  margin-bottom: -15px;
}

/* Arrow button styles */
.arrow {
  background-color: transparent;
  border: none;
  color: deepskyblue;
  font-size: 40px;
  cursor: pointer;
  position: absolute;
  padding-top: 500px;
  transform: translateY(-5%);
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
}

.arrow left {
  transform: translateX(50%);
  left: 30%; 
}

.arrow right {
  transform: translateX(50%);
  right: 30%; 
}

.arrow right:active {
  opacity: 0.1; 
}

.arrow left:active {
  opacity: 0.1; 
}